   ACRYLIC DESIGN SYSTEM
   Sistema completo de efectos acrlicos para Krause Insurance
   ======================================== */

/* ========================================
   NAVEGACIN
   ======================================== */

/* Navbar principal */
.navbar {
  backdrop-filter: blur(30px) saturate(170%);
  -webkit-backdrop-filter: blur(30px) saturate(170%);
}

/* Links de navegacin */
.nav-link:hover,
.nav-link.active {
  backdrop-filter: blur(15px) saturate(150%);
  -webkit-backdrop-filter: blur(15px) saturate(150%);
}

/* Sub-navegacin */
.sub-nav-btn:hover {
  backdrop-filter: blur(20px) saturate(160%);
  -webkit-backdrop-filter: blur(20px) saturate(160%);
}

.sub-nav-btn.active {
  backdrop-filter: blur(25px) saturate(170%);
  -webkit-backdrop-filter: blur(25px) saturate(170%);
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.1);
}

/* ========================================
   BOTONES
   ======================================== */

.btn-primary {
  backdrop-filter: blur(25px) saturate(170%);
  -webkit-backdrop-filter: blur(25px) saturate(170%);
}

.btn-primary:hover {
  backdrop-filter: blur(30px) saturate(180%);
  -webkit-backdrop-filter: blur(30px) saturate(180%);
}

.btn-secondary {
  backdrop-filter: blur(25px) saturate(160%);
  -webkit-backdrop-filter: blur(25px) saturate(160%);
}

.btn-secondary:hover {
  backdrop-filter: blur(30px) saturate(170%);
  -webkit-backdrop-filter: blur(30px) saturate(170%);
}

.btn-outline:hover {
  backdrop-filter: blur(20px) saturate(160%);
  -webkit-backdrop-filter: blur(20px) saturate(160%);
  box-shadow: 0 4px 12px var(--theme-primary-shadow-color), inset 0 1px 0 rgba(255, 255, 255, 0.1);
}

/* ========================================
   TARJETAS Y FEATURES
   ======================================== */

.feature-box {
  backdrop-filter: blur(30px) saturate(170%);
  -webkit-backdrop-filter: blur(30px) saturate(170%);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.1);
}

.feature-box:hover {
