  .nav-link,
  .sub-nav-btn,
  .btn-primary,
  .btn-secondary,
  .feature-box,
  .agent-card,
  .service-card-detailed,
  .dashboard-card,
  .dashboard-stat-card,
  .auth-card,
  .footer,
  .acrylic-light,
  .acrylic-medium,
  .acrylic-strong,
  .acrylic-intense,
  .acrylic-bg-white-light,
  .acrylic-bg-white-medium {
    background: rgba(255, 255, 255, 0.95);
  }

  .acrylic-bg-maroon {
    background: linear-gradient(135deg, var(--theme-secondary-rgba-95), var(--theme-primary-rgba-95));
  }
