___CSS_LOADER_EXPORT___.push([module.id, `/* Notifications (restored + tokenized)
   JS: src/modules/notifications.js
   Wrapper is created dynamically (#notification-wrapper). */

.notification-wrapper {
    position: fixed;
    top: calc(var(--nav-height) + 14px);
    right: 24px;
    z-index: 10000;
    display: flex;
    flex-direction: column;
    gap: 12px;
    pointer-events: none;
    /* allow clicks only on the notification itself */
}

.notification {
    pointer-events: auto;
    min-width: 280px;
    max-width: 360px;
    background: var(--theme-surface-bg);
    color: var(--theme-text-primary);
    border: 1px solid var(--theme-surface-border-strong);
    border-left: 5px solid var(--theme-accent-color);
    border-radius: 14px;
    padding: 14px 14px;
    box-shadow: var(--shadow-lg);
    display: flex;
    gap: 12px;
    align-items: flex-start;
    transform: translateX(110%);
    opacity: 0;
    transition: transform 0.28s ease, opacity 0.28s ease;
}

.notification.show {
    transform: translateX(0);
    opacity: 1;
}

.notification-body {
    flex: 1;
    font-size: 0.95rem;
    line-height: 1.35;
}

.notification-close {
    border: none;
    background: transparent;
    color: var(--theme-text-secondary);
    cursor: pointer;
    width: 28px;
    height: 28px;
    border-radius: 10px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    font-size: 1.2rem;
    line-height: 1;
}

.notification-close:hover {
    background: var(--theme-navlink-active-bg);
    color: var(--theme-text-primary);
}

.notification-success {
    border-left-color: var(--success);
}

.notification-error {
    border-left-color: var(--danger);
}

.notification-info {
    border-left-color: var(--info);
}

.notification-warning {
    border-left-color: var(--warning);
}

@media (max-width: 520px) {
    .notification-wrapper {
        left: 16px;
        right: 16px;
    }

    .notification {
        max-width: none;
        min-width: 0;
        width: 100%;
    }
}`, "",{"version":3,"sources":["webpack://./styles/notifications.css"],"names":[],"mappings":"AAAA;;4DAE4D;;AAE5D;IACI,eAAe;IACf,mCAAmC;IACnC,WAAW;IACX,cAAc;IACd,aAAa;IACb,sBAAsB;IACtB,SAAS;IACT,oBAAoB;IACpB,iDAAiD;AACrD;;AAEA;IACI,oBAAoB;IACpB,gBAAgB;IAChB,gBAAgB;IAChB,mCAAmC;IACnC,gCAAgC;IAChC,oDAAoD;IACpD,gDAAgD;IAChD,mBAAmB;IACnB,kBAAkB;IAClB,4BAA4B;IAC5B,aAAa;IACb,SAAS;IACT,uBAAuB;IACvB,2BAA2B;IAC3B,UAAU;IACV,oDAAoD;AACxD;;AAEA;IACI,wBAAwB;IACxB,UAAU;AACd;;AAEA;IACI,OAAO;IACP,kBAAkB;IAClB,iBAAiB;AACrB;;AAEA;IACI,YAAY;IACZ,uBAAuB;IACvB,kCAAkC;IAClC,eAAe;IACf,WAAW;IACX,YAAY;IACZ,mBAAmB;IACnB,oBAAoB;IACpB,mBAAmB;IACnB,uBAAuB;IACvB,iBAAiB;IACjB,cAAc;AAClB;;AAEA;IACI,0CAA0C;IAC1C,gCAAgC;AACpC;;AAEA;IACI,iCAAiC;AACrC;;AAEA;IACI,gCAAgC;AACpC;;AAEA;IACI,8BAA8B;AAClC;;AAEA;IACI,iCAAiC;AACrC;;AAEA;IACI;QACI,UAAU;QACV,WAAW;IACf;;IAEA;QACI,eAAe;QACf,YAAY;QACZ,WAAW;IACf;AACJ","sourcesContent":["/* Notifications (restored + tokenized)\n   JS: src/modules/notifications.js\n   Wrapper is created dynamically (#notification-wrapper). */\n\n.notification-wrapper {\n    position: fixed;\n    top: calc(var(--nav-height) + 14px);\n    right: 24px;\n    z-index: 10000;\n    display: flex;\n    flex-direction: column;\n    gap: 12px;\n    pointer-events: none;\n    /* allow clicks only on the notification itself */\n}\n\n.notification {\n    pointer-events: auto;\n    min-width: 280px;\n    max-width: 360px;\n    background: var(--theme-surface-bg);\n    color: var(--theme-text-primary);\n    border: 1px solid var(--theme-surface-border-strong);\n    border-left: 5px solid var(--theme-accent-color);\n    border-radius: 14px;\n    padding: 14px 14px;\n    box-shadow: var(--shadow-lg);\n    display: flex;\n    gap: 12px;\n    align-items: flex-start;\n    transform: translateX(110%);\n    opacity: 0;\n    transition: transform 0.28s ease, opacity 0.28s ease;\n}\n\n.notification.show {\n    transform: translateX(0);\n    opacity: 1;\n}\n\n.notification-body {\n    flex: 1;\n    font-size