.auth-section {
    min-height: calc(100vh - 80px);
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 40px 24px;
}

.auth-container {
    width: 100%;
    max-width: 480px;
}

.auth-card {
    background: var(--theme-surface-bg-solid);
    border-radius: 24px;
    padding: 48px 40px;
    box-shadow: var(--shadow-lg);
}

.auth-header {
    text-align: center;
    margin-bottom: 40px;
}

.logo-shield-auth {
    color: var(--theme-primary-color);
    margin: 0 auto 20px;
    /* center the SVG in its container */
    display: block;
    filter: drop-shadow(0 4px 8px var(--theme-primary-shadow-color));
}

.auth-header h2 {
    font-family: 'Cinzel', serif;
    font-size: 1.8rem;
    color: var(--theme-text-primary);
    margin-bottom: 8px;
}

.auth-header p {
    color: var(--theme-text-secondary);
}

.auth-form {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.auth-divider {
    display: flex;
    align-items: center;
    gap: 12px;
    margin: 22px 0 14px;
    color: var(--text-light);
    opacity: 0.9;
}

.auth-divider::before,
.auth-divider::after {
    content: '';
    flex: 1;
    height: 1px;
    background: rgba(0, 0, 0, 0.08);
}

.auth-divider span {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 28px;
    height: 28px;
    border-radius: 999px;
    background: var(--gray-100);
    color: var(--text-light);
    font-weight: 700;
    font-size: 0.85rem;
    box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.7);
}

.auth-footer-text {
    margin: 0;
    text-align: center;
    color: var(--theme-text-secondary);
    font-size: 0.95rem;
    line-height: 1.35;
}

.auth-footer-text+.auth-footer-text {
    margin-top: 10px;
}

.auth-link {
    color: var(--theme-primary-color);
    text-decoration: none;
    font-weight: 700;
}

.auth-link:hover {
    color: var(--theme-secondary-color);
    text-decoration: underline;
    text-underline-offset: 3px;
}

.form-group {
    display: flex;
    flex-direction: column;
    gap: 4px;
}

.form-label {
    font-weight: 600;
    color: var(--theme-text-primary);
    font-size: 0.7rem;
}

.form-control {
    padding: 14px 16px;
    border: 2px solid var(--gray-200);
    border-radius: 10px;
    font-size: 15px;
    font-family: inherit;
    background: var(--theme-surface-bg-solid);
    color: var(--theme-text-primary);
    transition: all 0.3s ease;
}

/* Prevent browser autofill from forcing yellow backgrounds / low-contrast text.
   This is especially noticeable on dark themes like dark-forest. */
input.form-control:-webkit-autofill,
input.form-control:-webkit-autofill:hover,
input.form-control:-webkit-autofill:focus,
textarea.form-control:-webkit-autofill,
textarea.form-control:-webkit-autofill:hover,
textarea.form-control:-webkit-autofill:focus {
    -webkit-text-fill-color: var(--theme-text-primary);
    caret-color: var(--theme-text-primary);
    box-shadow: 0 0 0px 1000px var(--theme-surface-bg-solid) inset;
    transition: background-color 9999s ease-in-out 0s;
}

input.form-control:-moz-autofill,
input.form-control:-moz-autofill:hover,
input.form-control:-moz-autofill:focus,
textarea.form-control:-moz-autofill,
textarea.form-control:-moz-autofill:hover,
textarea.form-control:-moz-autofill:focus {
    box-shadow: 0 0 0px 1000px var(--theme-surface-bg-solid) inset;
    caret-color: var(--theme-text-primary);
}

.form-control:focus {
    outline: none;
    border-color: var(--theme-primary-color);
    background: rgba(255, 255, 255, 0.98);
    backdrop-filter: blur(20px) saturate(150%);
    -webkit-backdrop-filter: blur(20px) saturate(150%);
    box-shadow: 0 0 0 3px var(--theme-surface-border), 0 4px 12px var(--theme-primary-shadow-color);
}

.form-options {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 0.9rem;
}

.checkbox-label {
    display: flex;
    align-items: center;
    gap: 8px;
    cursor: pointer;
}

.link-text {
    color: var(--theme-primary-color);
    text-decoration: none;
    font-weight: 600;
    transition: color 0.3s ease;
}

.link-text:hover {
    color: var(--theme-secondary-color);
}

.auth-footer {
    text-align: center;
    margin-top: 24px;
    color: var(--theme-text-secondary);
}

.demo-credentials {
    margin-top: 24px;
    padding: 16px;
    background: var(--gray-100);
    border-radius: 10px;
    font-size: 0.85rem;
    text-align: center;
    color: var(--theme-text-secondary);
}

.demo-credentials strong {
    color: var(--theme-text-primary);
}`, "",{"version":3,"sources":["webpack://./styles/auth.css"],"names":[],"mappings":"AAAA,iBAAiB;AACjB;IACI,8BAA8B;IAC9B,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,kBAAkB;AACtB;;AAEA;IACI,WAAW;IACX,gBAAgB;AACpB;;AAEA;IACI,yCAAyC;IACzC,mBAAmB;IACnB,kBAAkB;IAClB,4BAA4B;AAChC;;AAEA;IACI,kBAAkB;IAClB,mBAAmB;AACvB;;AAEA;IACI,iCAAiC;IACjC,mBAAmB;IACnB,oCAAoC;IACpC,cAAc;IACd,gEAAgE;AACpE;;AAEA;IACI,4BAA4B;IAC5B,iBAAiB;IACjB,gCAAgC;IAChC,kBAAkB;AACtB;;AAEA;IACI,kCAAkC;AACtC;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,SAAS;IACT,mBAAmB;IACnB,wBAAwB;IACxB,YAAY;AAChB;;AAEA;;IAEI,WAAW;IACX,OAAO;IACP,WAAW;IACX,+BAA+B;AACnC;;AAEA;IACI,oBAAoB;IACpB,mBAAmB;IACnB,uBAAuB;IACvB,WAAW;IACX,YAAY;IACZ,oBAAoB;IACpB,2BAA2B;IAC3B,wBAAwB;IACxB,gBAAgB;IAChB,kBAAkB;IAClB,kDAAkD;AACtD;;AAEA;IACI,SAAS;IACT,kBAAkB;IAClB,kCAAkC;IAClC,kBAAkB;IAClB,iBAAiB;AACrB;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,iCAAiC;IACjC,qBAAqB;IACrB,gBAAgB;AACpB;;AAEA;IACI,mCAAmC;IACnC,0BAA0B;IAC1B,0BAA0B;AAC9B;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,QAAQ;AACZ;;AAEA;IACI,gBAAgB;IAChB,gCAAgC;IAChC,iBAAiB;AACrB;;AAEA;IACI,kBAAkB;IAClB,iCAAiC;IACjC,mBAAmB;IACnB,eAAe;IACf,oBAAoB;IACpB,yCAAyC;IACzC,gCAAgC;IAChC,yBAAyB;AAC7B;;AAEA;mEACmE;AACnE;;;;;;IAMI,kDAAkD;IAClD,sCAAsC;IACtC,8DAA8D;IAC9D,iDAAiD;AACrD;;AAEA;;;;;;IAMI,8DAA8D;IAC9D,sCAAsC;AAC1C;;AAEA;IACI,aAAa;IACb,wCAAwC;IACxC,qCAAqC;IACrC,0CAA0C;IAC1C,kDAAkD;IAClD,+FAA+F;AACnG;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,iBAAiB;AACrB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,QAAQ;IACR,eAAe;AACnB;;AAEA;IACI,iCAAiC;IACjC,qBAAqB;IACrB,gBAAgB;IAChB,2BAA2B;AAC/B;;AAEA;IACI,mCAAmC;AACvC;;AAEA;IACI,kBAAkB;IAClB,gBAAgB;IAChB,kCAAkC;AACtC;;AAEA;IACI,gBAAgB;IAChB,aAAa;IACb,2BAA2B;IAC3B,mBAAmB;IACnB,kBAAkB;IAClB,kBAAkB;IAClB,kCAAkC;AACtC;;AAEA;IACI,gCAAgC;AACpC","sourcesContent":["/* Auth Section */\n.auth-section {\n    min-height: calc(100vh - 80px);\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    padding: 40px 24px;\n}\n\n.auth-container {\n  