   This file provides the missing layout/components for dashboards:
   - grid layout
   - cards + headers
   - lists (policies/quotes)
   - tables
   - badges
   - icon buttons
   - sidebar widgets
*/

/* Utilities for dashboard buttons */
.btn-sm {
    padding: 8px 12px;
    border-radius: 10px;
    font-size: 0.88rem;
    line-height: 1.1;
}

.btn-icon {
    width: 36px;
    height: 36px;
    border-radius: 10px;
    border: 1px solid var(--theme-surface-border);
    background: var(--acrylic-bg-light);
    color: var(--theme-text-primary);
    cursor: pointer;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    transition: transform 0.2s ease, background 0.2s ease, border-color 0.2s ease;
}

.btn-icon:hover {
    transform: translateY(-1px);
    background: var(--acrylic-bg-medium);
    border-color: var(--theme-surface-border-strong);
}

/* Layout */
.dashboard-grid {
    display: grid;
    grid-template-columns: 1fr 350px;
    gap: 24px;
}

.dashboard-main {
    min-width: 0;
}

.dashboard-sidebar {
    min-width: 0;
}

/* Cards */
.dashboard-card {
    background: var(--theme-surface-bg);
    border: 1px solid var(--theme-surface-border);
    border-radius: 16px;
    padding: 28px;
    box-shadow: var(--shadow-sm);
    margin-bottom: 24px;
    color: var(--theme-text-primary);
}

/* Simple card title when no .card-header is used (sidebar cards) */
.dashboard-card>h3 {
    font-family: 'Cinzel', serif;
    font-size: 1.25rem;
    color: var(--theme-text-primary);
    margin: 0 0 16px 0;
}

.card-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 12px;
    margin-bottom: 20px;
    padding-bottom: 14px;
    border-bottom: 2px solid var(--theme-surface-border);
}

.card-header h3 {
    font-family: 'Cinzel', serif;
    font-size: 1.25rem;
    color: var(--theme-text-primary);
    margin: 0;
}

/* Lists */
.policies-list,
.quotes-list,
.documents-list,
.tasks-list {
    display: flex;
    flex-direction: column;
    gap: 14px;
}

.policy-item,
.quote-item {
    display: flex;
    align-items: center;
    gap: 16px;
    padding: 18px;
    background: var(--acrylic-bg-medium);
    border: 1px solid var(--theme-surface-border);
    border-radius: 12px;
    transition: transform 0.2s ease, background 0.2s ease, border-color 0.2s ease, box-shadow 0.2s ease;
}

.policy-item:hover,
.quote-item:hover {
    background: var(--theme-navlink-active-bg);
    border-color: var(--theme-surface-border-strong);
    transform: translateX(3px);
    box-shadow: var(--shadow);
}

.policy-icon {
    font-size: 2rem;
    width: 52px;
    height: 52px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--acrylic-bg-light);
    border: 1px solid var(--theme-surface-border);
    border-radius: 12px;
    flex-shrink: 0;
}

.policy-info,
.quote-info {
    flex: 1;
    min-width: 0;
}

.policy-actions {
    display: flex;
    align-items: center;
    gap: 10px;
    flex-shrink: 0;
}

.policy-info h4,
.quote-info h4 {
    font-size: 1.05rem;
    color: var(--theme-text-primary);
    margin: 0 0 6px 0;
}

.policy-info p,
.quote-info p {
    color: var(--theme-text-secondary);
    font-size: 0.92rem;
    margin: 0 0 10px 0;
}

.policy-meta {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 10px;
    font-size: 0.85rem;
    color: var(--theme-text-secondary);
}

.quote-date {
    font-size: 0.85rem;
    color: var(--theme-text-secondary);
}

/* Badges */
.badge {
    padding: 4px 12px;
    border-radius: 999px;
    font-size: 0.72rem;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 0.5px;
}

.badge-success {
    background: var(--success);
    color: var(--theme-on-success);
}

.badge-warning {
    background: var(--warning);
    color: var(--theme-on-warning);
}

.badge-danger {
    background: var(--danger);
    color: var(--theme-on-danger);
}

.badge-primary {
    background: var(--theme-primary-color);
    color: var(--theme-highlight-light);
}

.badge-accent {
    background: var(--theme-accent-bg-soft);
    color: var(--theme-accent-color);
    border: 1px solid var(--theme-accent-border);
}

/* Tables */
.payments-table table,
.clients-table table {
    width: 100%;
    border-collapse: collapse;
}

.payments-table th,
.payments-table td,
.clients-table th,
.clients-table td {
    padding: 14px 12px;
    text-align: left;
    border-bottom: 1px solid var(--theme-surface-border);
}

.payments-table th,
.clients-table th {
    font-weight: 700;
    color: var(--theme-text-primary);
    background: var(--theme-navlink-active-bg);
    font-size: 0.9rem;
}

.payments-table td,
.clients-table td {
    color: var(--theme-text-secondary);
    font-size: 0.95rem;
}

.client-cell {
    display: flex;
    align-items: center;
    gap: 12px;
}

.client-avatar {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: linear-gradient(135deg, var(--theme-primary-color), var(--theme-accent-color));
    color: var(--white);
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 800;
    font-size: 0.9rem;
    flex-shrink: 0;
}

.client-name {
    font-weight: 700;
    color: var(--theme-text-primary);
}

.client-email {
    font-size: 0.85rem;
    color: var(--theme-text-secondary);
}

/* Sidebar */
.dashboard-sidebar .dashboard-card {
    position: sticky;
    top: calc(var(--nav-height) + 14px);
}

.quick-actions {
    display: flex;
    flex-direction: column;
    gap: 12px;
}

.agent-info {
    text-align: center;
}

.agent-avatar {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    background: linear-gradient(135deg, var(--theme-primary-color), var(--theme-accent-color));
    color: var(--white);
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'Cinzel', serif;
    font-size: 2rem;
    font-weight: 800;
    margin: 0 auto 16px;
    box-shadow: 0 10px 26px var(--theme-primary-shadow-color);
}

.agent-info h4 {
    font-family: 'Cinzel', serif;
    font-size: 1.15rem;
    margin: 0 0 6px 0;
    color: var(--theme-text-primary);
}

.agent-info p {
    color: var(--theme-text-secondary);
    font-size: 0.92rem;
    margin: 0 0 14px 0;
}

.agent-contact {
    margin: 14px 0;
    padding: 14px;
    background: var(--theme-navlink-active-bg);
    border: 1px solid var(--theme-surface-border);
    border-radius: 12px;
    text-align: left;
}

.agent-contact p {
    margin: 6px 0;
    font-size: 0.92rem;
    color: var(--theme-text-secondary);
}

.document-link {
    padding: 12px 14px;
    background: var(--theme-navlink-active-bg);
    border: 1px solid var(--theme-surface-border);
    border-radius: 10px;
    text-decoration: none;
    color: var(--theme-text-primary);
    font-size: 0.92rem;
    transition: transform 0.2s ease, background 0.2s ease;
    display: flex;
    align-items: center;
    gap: 10px;
}

.document-link:hover {
    background: var(--acrylic-bg-medium);
    transform: translateX(3px);
}

.task-item {
    display: flex;
    align-items: center;
    gap: 12px;
    padding: 12px;
    background: var(--theme-navlink-active-bg);
    border: 1px solid var(--theme-surface-border);
    border-radius: 10px;
}

.task-item input[type="checkbox"] {
    width: 18px;
    height: 18px;
    cursor: pointer;
}

.commissions-info {
    display: flex;
    flex-direction: column;
    gap: 12px;
}

.commission-row {
    display: flex;
    justify-content: space-between;
    gap: 12px;
    padding: 10px 0;
    border-bottom: 1px solid var(--theme-surface-border);
    color: var(--theme-text-secondary);
}

.commission-row strong {
    color: var(--theme-text-primary);
}

/* Responsive */
@media (max-width: 1100px) {
    .dashboard-grid {
        grid-template-columns: 1fr;
    }

    .dashboard-sidebar .dashboard-card {
        position: static;
    }
}

@media (max-width: 560px) {
    .dashboard-card {
        padding: 20px;
    }

    .policy-item,
    .quote-item {
        flex-direction: column;
        align-items: flex-start;
    }
